@font-face {
  font-family: 'ubuntu-regular';
  src: local("Ubuntu Regular");
  src: url("/static/fonts/ubuntu-regular.ttf") format("truetype"); }

@font-face {
  font-family: 'ubuntu-medium';
  src: local("Ubuntu Medium");
  src: url("/static/fonts/ubuntu-medium.ttf") format("truetype"); }

@keyframes showDetail {
  0% {
    opacity: .5;
    transform: translateY(600px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes backDrop {
  0% {
    opacity: 0;
    transform: translateY(-300px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

.detail {
  position: relative;
  margin-top: 30vh;
  height: 85vh;
  padding-bottom: 10px;
  background: #00A0E4;
  animation: backDrop .6s ease; }
  .detail section {
    display: block;
    position: absolute;
    top: -15vh;
    left: 50%;
    margin-left: -45%;
    margin-bottom: 25px;
    width: 90vw;
    min-height: 460px;
    background: #FFF;
    padding: 20px;
    border-radius: 10px;
    box-sizing: border-box;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
    animation: showDetail .4s ease; }
    .detail section > img {
      position: absolute;
      top: -75px;
      left: 50%;
      margin-left: -75px;
      border-radius: 150px;
      box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3); }
    .detail section h1 {
      margin-top: 75px;
      font: 1em 'ubuntu-medium', sans-serif;
      color: #00A0E4;
      text-transform: uppercase;
      text-align: center; }
    .detail section p {
      font: .9em 'ubuntu-regular', sans-serif;
      color: #262626; }
      .detail section p::first-letter {
        text-transform: uppercase; }
    .detail section dl {
      margin-top: 30px; }
      .detail section dl dt {
        float: left;
        margin-right: 15px; }
        .detail section dl dt img {
          width: 64px;
          height: 64px;
          border-radius: 64px;
          box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3); }
      .detail section dl dd {
        font: .9em 'ubuntu-regular', sans-serif;
        color: #262626; }
        .detail section dl dd small {
          display: block;
          font: .8em 'ubuntu-regular', sans-serif;
          text-transform: uppercase;
          color: #6E6E6E; }
        .detail section dl dd b {
          font: 1em 'ubuntu-medium', sans-serif;
          color: #00A0E4;
          text-transform: uppercase; }
        .detail section dl dd address {
          display: block;
          margin-top: 5px;
          font: .9em 'ubuntu-regular', sans-serif;
          text-transform: lowercase;
          color: #262626; }
