@font-face {
  font-family: 'ubuntu-regular';
  src: local("Ubuntu Regular");
  src: url("/static/fonts/ubuntu-regular.ttf") format("truetype"); }

@font-face {
  font-family: 'ubuntu-medium';
  src: local("Ubuntu Medium");
  src: url("/static/fonts/ubuntu-medium.ttf") format("truetype"); }

body {
  margin: 0;
  background: #FFD400; }

header {
  padding: 20px 20px 0; }
  header h1 {
    width: 100px;
    height: 56px;
    margin: 0 auto;
    background: url("/static/images/logo-petz-small.png") no-repeat;
    background-size: 100px;
    text-indent: -9000px; }
    @media screen and (min-width: 1024px) {
      header h1 {
        width: 150px;
        height: 105px;
        background: url("/static/images/logo-petz-large.png") no-repeat;
        background-size: 150px; } }
